@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Caveat:wght@500&family=Chango&family=Indie+Flower&family=Open+Sans+Condensed:wght@300&family=Oswald:wght@300&family=Roboto:wght@300&family=Syne+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.app-container {
  width: 100vw;
  height: 100vh;
  background-image: url("https://i.pinimg.com/originals/e6/66/83/e66683612ce467134ed4a3be9d0ce28e.png");
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
}

@media (max-width: 900px) {
  .app-container {
    background-image: url("https://i.imgur.com/yaWxbVl.jpg");
    background-position: center;
  }
}

.app-container .navbar-wrapper {
  display: flex;
  width: 100vw;
  justify-content: center;
}

.app-container .navbar-wrapper .navbar {
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 50vw;
  height: 125px;
  min-width: 850px;
  padding: 20px;
  padding-bottom: 60px;
  border-bottom: 1px solid #ff4646;
  overflow: hidden;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar {
    flex-direction: column;
    justify-content: space-around;
    padding: 10px;
    height: 160px;
  }
}

.app-container .navbar-wrapper .navbar .search-bar-component {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar .search-bar-component {
    flex-direction: column;
    overflow: hidden;
  }
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-input {
  width: 400px;
  height: 45px;
  background-color: #2c2c2c;
  border: none;
  border-bottom: 2px solid #ff0000;
  border-top: 2px solid transparent;
  border-left: 2px solid transparent;
  color: white;
  transition: all 0.6s;
  outline: none;
  letter-spacing: 1px;
  text-decoration: none;
  text-align: center;
  font-size: 18px;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar .search-bar-component .search-input {
    width: 80vw;
    padding: 20px;
  }
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-input::-webkit-input-placeholder {
  color: #C5C5CD;
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-input:-ms-input-placeholder {
  color: #C5C5CD;
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-input::placeholder {
  color: #C5C5CD;
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-input:focus {
  border-top: 2px solid #ff0000;
  border-left: 2px solid #ff0000;
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Bebas Neue', cursive;
  width: 100px;
  height: 45px;
  background-color: #171717;
  background-image: none;
  border: 2px solid #C5C5CD;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  color: white;
  transition: all 0.6s;
  outline: none;
  letter-spacing: 1px;
  text-decoration: none;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar .search-bar-component .search-button {
    width: 80vw;
    padding: 20px;
    overflow: hidden;
  }
}

.app-container .navbar-wrapper .navbar .search-bar-component .search-button:hover {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  letter-spacing: 5px;
}

.app-container .navbar-wrapper .navbar .navigation-section {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  margin-left: 200px;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar .navigation-section {
    margin-left: 0px;
    overflow: hidden;
  }
}

.app-container .navbar-wrapper .navbar .navigation-section .navigation-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar .navigation-section .navigation-wrapper {
    margin-top: 10px;
  }
}

.app-container .navbar-wrapper .navbar .navigation-section .navigation-wrapper .navigation-buttons {
  height: 45px;
  width: 100px;
  background-color: #171717;
  background-image: none;
  border: 2px solid #C5C5CD;
  font-family: 'Bebas Neue', cursive;
  border-radius: 7px;
  color: white;
  padding: 10px;
  transition: all 0.6s;
  outline: none;
  letter-spacing: 1px;
  text-decoration: none;
  text-align: center;
}

@media (max-width: 900px) {
  .app-container .navbar-wrapper .navbar .navigation-section .navigation-wrapper .navigation-buttons {
    width: 30vw;
  }
}

.app-container .navbar-wrapper .navbar .navigation-section .navigation-wrapper .navigation-buttons:hover {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  letter-spacing: 5px;
}

.app-container .navbar-wrapper .navbar .navigation-section .navigation-wrapper .current-page-number {
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 25px;
  font-family: 'Syne Mono', monospace;
}

.app-container .people-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  overflow-y: hidden;
}

.app-container .people-container .person-container {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 350px;
  background-color: #2c2c2c;
  align-items: center;
  margin-top: 150px;
  margin-bottom: 40px;
  border-radius: 17px;
}

@media (max-width: 900px) {
  .app-container .people-container .person-container {
    margin-top: 170px;
  }
}

@media (max-width: 500px) {
  .app-container .people-container .person-container {
    width: 90vw;
  }
}

.app-container .people-container .person-container .person-name {
  font-family: 'Oswald', sans-serif;
  letter-spacing: 4px;
  text-align: center;
  padding: 20px;
  color: white;
  width: 100%;
  border-bottom: 2px solid #ff0000;
}

.app-container .people-container .person-container .person-main-content {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.app-container .people-container .person-container .person-main-content .person-content-section {
  width: 50%;
  pad: 15px;
  text-align: center;
}

.app-container .people-container .person-container .person-main-content .person-content-section .person-content-wrapper {
  padding: 15px;
}

.app-container .people-container .person-container .person-main-content .person-content-section .person-content-wrapper .person-content-title {
  color: white;
  letter-spacing: 1px;
  padding: 10px;
}

.app-container .people-container .person-container .person-main-content .person-content-section .person-content-wrapper .person-content-data {
  background-color: white;
  color: #ff0000;
  padding: 8px;
  border-radius: 7px;
  font-family: 'Bebas Neue', cursive;
  font-size: 22px;
  letter-spacing: 2px;
}

.app-container .people-container .person-container .page-button {
  width: 50%;
  background-color: #171717;
  background-image: none;
  border: 2px solid #C5C5CD;
  border-radius: 7px;
  color: #C5C5CD;
  padding: 10px;
  margin-top: 20px;
  transition: all 0.6s;
  outline: none;
  letter-spacing: 1px;
  text-decoration: none;
  text-align: center;
}

.app-container .people-container .person-container .page-button:hover {
  background-image: linear-gradient(to right, #C5C5CD, #ff4646, #C5C5CD);
  color: white;
  cursor: pointer;
  letter-spacing: 5px;
}

.app-container #back-button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Bebas Neue', cursive;
  width: 100px;
  height: 45px;
  background-color: #171717;
  border: 2px solid #C5C5CD;
  border-radius: 7px;
  color: white;
  transition: all 0.6s;
  margin-top: 10px;
  outline: none;
  letter-spacing: 1px;
  text-decoration: none;
}

.app-container #back-button:hover {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  letter-spacing: 5px;
}

.app-container #loader {
  width: 200px;
  height: 200px;
  margin-left: 45%;
  margin-top: 20%;
  text-align: center;
  z-index: 10;
  color: #ff0000;
  font-family: 'Bebas Neue', cursive;
  font-size: 50px;
  animation-name: loader;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@media (max-width: 900px) {
  .app-container #loader {
    margin-left: 35%;
    margin-top: 50%;
  }
}

@keyframes loader {
  25% {
    color: #fd0000;
    letter-spacing: 3px;
    width: 250px;
  }
  50% {
    color: white;
    letter-spacing: 6px;
    width: 300px;
  }
  100% {
    color: red;
    letter-spacing: 0px;
    width: 200px;
  }
}

.app-container #error-block {
  width: 400px;
  height: 200px;
  margin-top: 20%;
  margin-left: 0%;
  text-align: center;
  z-index: 10;
  color: #ff0000;
  font-family: 'Bebas Neue', cursive;
  font-size: 50px;
}

@media (max-width: 900px) {
  .app-container #error-block {
    margin-left: 10%;
    margin-top: 50%;
  }
}

.app-container #no-character-error {
  width: 400px;
  height: 200px;
  margin-top: 20%;
  margin-left: 0%;
  text-align: center;
  z-index: 10;
  color: #ff0000;
  font-family: 'Bebas Neue', cursive;
  font-size: 50px;
}

@media (max-width: 900px) {
  .app-container #no-character-error {
    margin-left: 10%;
    margin-top: 50%;
  }
}
